import React from 'react'
import style from './styles.module.css'
import clsx from 'clsx'
import Image from 'next/image'
import { InlineLink } from '@components/atoms/InlineLink/Component'
import { Case } from '@generated/graphql-request'
import ReactMarkdown from 'react-markdown'
import { getWebsiteApiOrigin } from '@misc/environments'

interface Props {
  isReversed?: boolean
  color?: string
}

export default function CaseLarge (props: Props & Case): JSX.Element {
  return (
    <div className={clsx(style.grid, style.caseLarge)}>
        <div className={clsx(style.contentCol, props.isReversed && style.isReversed)}>
          <h2>{props.teaserTitle}<span className="span-pink">.</span></h2>
          <h6>{props.teaserSubtitle}</h6>
          <ReactMarkdown>{props.teaserDescription ?? ''}</ReactMarkdown>
          <InlineLink as="link">{props.teaserButtonLabel}</InlineLink>
        </div>

        <div className={clsx(style.imageCol, props.isReversed && style.isReversed)}>
          <div className={clsx(
            style.bgBlock,
            props.color === 'blue' && style.bgBlockBlue,
            props.color === 'pink' && style.bgBlockPink,
            props.color === 'orange' && style.bgBlockOrange,
            props.color === 'red' && style.bgBlockRed,
            props.color === 'green' && style.bgBlockGreen,
            props.color === 'purple' && style.bgBlockPurple,
            props.color === 'lavender' && style.bgBlockLavender,
          )} />
          <div className={style.imageWrapper}>
            <Image src={`${getWebsiteApiOrigin()}${props.teaserImage?.data?.attributes?.url ?? ''}`} alt={props.teaserImage?.data?.attributes?.caption ?? ''} layout='fill' objectFit='cover' objectPosition='center center' />          
          </div>
        </div>
    </div>
  )
}
